import React from 'react';
import s from './Header.module.css';

const Header = () => {
    return (
        <header className={s.header}>
            <img alt='logo company'
                 src='https://cdn3.iconfinder.com/data/icons/picons-social/57/56-apple-512.png'/>
        </header>
    );
};

export default Header;